export function showToolDiv() {
    document.getElementById("tool-button").style.display = "none"
    document.getElementById("tool-div").style.display = "inline-block"  
}

export function hideToolDiv() {
    document.getElementById("tool-button").style.display = "inline-block"
    document.getElementById("tool-div").style.display = "none"
}

export function showHelpDiv() {
    document.getElementById("help-button").style.display = "none"
    document.getElementById("help-div").style.display = "inline-block"  
}

export function hideHelpDiv()  {
    document.getElementById("help-button").style.display = "inline-block"
    document.getElementById("help-div").style.display = "none"
}

export function showTableDiv() {
    document.getElementById("table-open").style.display = "none"
    document.getElementById("table-close").style.display = "block"
    document.getElementById("table-body").style.display = "block"
}

export function hideTableDiv() {
    document.getElementById("table-open").style.display = "block";
    document.getElementById("table-close").style.display = "none";
    document.getElementById("table-body").style.display = "none";
}

